/* eslint-disable */
import { defineStore, storeToRefs } from "pinia"
import { useLanguage } from '@/store/Language'
import units from '@/assets/json/Units.json'
import UI_language from '@/assets/json/Localization/LanguageSource_UI.json'
import Unit_language from '@/assets/json/Localization/LanguageSource_Units.json'
import axios from 'axios'

export const useInitUnit = defineStore('InitUnit', {
    state: () => {
        return {
            classTypeList:['Guardian', 'Mage', 'Sage', 'Assassin', 'Ranger', 'Warrior'],
            elementTypeList:['Nature', 'Water', 'Fire', 'Light', 'Dark'],
            damageTypeList:['Physical', 'Magic'],
            searchFilter:'',
            classFilter:'',
            elementFilter:'',
            units:units,
            unit:[],
            ui_card_path:'',
            baseATK:0,
            baseDEF:0,
            baseHP:0,
            havenObj:{
                "Nature":"UI_Icon_LifeAffinity.png",
                "Water":"UI_Icon_MindAffinity.png",
                "Fire":"UI_Icon_HeartAffinity.png",
                "Light":"UI_Icon_DayAffinity.png",
                "Dark":"UI_Icon_NightAffinity.png",
                "MaterialItem_ClassMat_Guardian":"UI_Icon_Item_Anvil.png",
                "MaterialItem_ClassMat_Mage":"UI_Icon_Item_CrystalBall.png",
                "MaterialItem_ClassMat_Sage":"UI_Icon_Item_MagicalElixir.png",
                "MaterialItem_ClassMat_Assassin":"UI_Icon_Item_GoldenApple.png",
                "MaterialItem_ClassMat_Ranger":"UI_Icon_Item_Silvercoin.png",
                "MaterialItem_ClassMat_Warrior":"UI_Icon_Item_MagicalPowder.png",
                "ExpItem_Basic":"UI_Icon_Item_Exp1.png"
            },
        }
    },
    actions: {
        setLanguage() {
            this.units.forEach((obj) => {
                obj.name = Unit_language.mSource.mTerms.filter(item => item.Term === `UnitName/${obj.number}`)[0].Languages[useLanguage().language]
                this.classFilter = UI_language.mSource.mTerms.filter(item => item.Languages[0] === 'Class')[0].Languages[useLanguage().language]
                this.elementFilter = UI_language.mSource.mTerms.filter(item => item.Languages[0] === 'Element')[0].Languages[useLanguage().language]
                axios.get(`json/UnitData/${obj.unitData}`)
                    .then((response) => {
                        obj.classType = UI_language.mSource.mTerms.filter(item => item.Languages[0] === this.classTypeList[response.data.unitStatData.classType])[0].Languages[useLanguage().language]
                        obj.elementType = UI_language.mSource.mTerms.filter(item => item.Languages[0].includes(">" + this.elementTypeList[response.data.unitStatData.unitElement]))[0].Languages[useLanguage().language].split('>')[1]
                    })
            })
        },
        initialUnit(item) {
            console.log(Unit_language.mSource.mTerms.filter(item => item.Term.includes("UnitName/CHAR")))
            if (item != undefined) {
                this.unit = this.units[this.units.findIndex(obj => obj.displayName === item.displayName)]
            }
            if (this.unit === undefined) {
                console.log('redirect')
            }else {
                axios.get(`json/UnitData/${this.unit.unitData}`)
                    .then((response) => {
                        this.ui_card_path     = `img/UI_Card_S/UI_Card_${this.unit.imgName}_S.png`
                        this.unit.classImg    = UI_language.mSource.mTerms.filter(item => item.Languages[0] === this.classTypeList[response.data.unitStatData.classType])[0].Languages[0]
                        this.unit.elementImg  = UI_language.mSource.mTerms.filter(item => item.Languages[0].includes(">" + this.elementTypeList[response.data.unitStatData.unitElement]))[0].Languages[0].split('>')[1]
                        
                        this.unit.name        = Unit_language.mSource.mTerms.filter(item => item.Term === `UnitName/${this.unit.number}`)[0].Languages[useLanguage().language]
                        this.unit.classType   = UI_language.mSource.mTerms.filter(item => item.Languages[0] === this.classTypeList[response.data.unitStatData.classType])[0].Languages[useLanguage().language]
                        this.unit.elementType = UI_language.mSource.mTerms.filter(item => item.Languages[0].includes(">" + this.elementTypeList[response.data.unitStatData.unitElement]))[0].Languages[useLanguage().language].split('>')[1]
                        this.unit.damageType  = UI_language.mSource.mTerms.filter(item => item.Languages[0] === this.damageTypeList[response.data.unitStatData.attackDamage.DamageType])[0].Languages[useLanguage().language]
                        
                        this.unit.star        = response.data.unitData.startingTier
                        this.unit.dp          = response.data.unitStatData.deployCost
                        this.unit.energyGain  = response.data.unitStatData.energyGainPerAttack
                        this.unit.aspd        = response.data.unitStatData.attackSpeed
                        this.unit.range       = response.data.unitStatData.attackRange
                        this.unit.mspd        = response.data.unitStatData.moveSpeed
                        this.unit.crit        = response.data.unitStatData.critChance
                        this.unit.cdmg        = response.data.unitStatData.critDamage
                        this.unit.eva         = response.data.unitStatData.baseEvasionPercent
                        this.unit.acc         = response.data.unitStatData.baseAccuracyPercent
                        this.unit.pres        = response.data.unitStatData.physResist
                        this.unit.mres        = response.data.unitStatData.magicResist
                        this.unit.resilience  = response.data.unitStatData.resilience
                        this.unit.lifeSteal   = response.data.unitStatData.hpAbsorb
                        this.unit.healBonus   = response.data.unitStatData.healBonus

                        this.baseATK          = response.data.unitStatData.attackDamage.damageScaling.yIntercept
                        this.baseDEF          = response.data.unitStatData.defense.yIntercept
                        this.baseHP           = response.data.unitStatData.hp.yIntercept
                    });
            }
        },
        clearSearch() {
            this.searchFilter = ''
        },
        setHavenReward() {
            this.units.forEach((item) => {
                axios.get(`json/UnitData/${item.unitData}`)
                    .then((response) => {
                        item.dropItem1 = this.havenObj[(response.data.unitData.havenRewards[0].objectReference.objectID).replace("MaterialItem_HavenElementAffinity", "")]
                        item.dropRate1 = response.data.unitData.havenRewards[0].dropOptionExtension.individualDropPercent
                        item.maxDrop1 = response.data.unitData.havenRewards[0].dropOptionExtension.maxDrop
                        item.minDrop1 = response.data.unitData.havenRewards[0].dropOptionExtension.minDrop
                        item.dropItem2 = this.havenObj[response.data.unitData.havenRewards[1].objectReference.objectID]
                        item.dropRate2 = response.data.unitData.havenRewards[1].dropOptionExtension.individualDropPercent
                        item.maxDrop2 = response.data.unitData.havenRewards[1].dropOptionExtension.maxDrop
                        item.minDrop2 = response.data.unitData.havenRewards[1].dropOptionExtension.minDrop
                        if (!this.havenObj[response.data.unitData.havenRewards[1].objectReference.objectID]) {
                            axios.get(`json/EquipmentItem/${response.data.unitData.havenRewards[1].objectReference.objectID}.json`)
                                .then((response2) => {
                                    item.dropItem2 = `${response2.data.itemSpriteName}.png`
                                })
                        }
                    });
            })
        },
    },
})