<template>
    <div class="col-md-2 side-nav" :class="{ in: !isSideNav, out: isSideNav}">
        <nav class="nav flex-column my-3" style="position: relative;">
          <form class="d-flex" role="search">
            <input class="form-control me-2" type="search" :placeholder="text.search" aria-label="Search" v-model="searchFilter">
          </form>
          <div class="me-2 mb-3">
            <select class="form-select mt-2" v-model="elementFilter" @click="clearSearch">
              <option selected :value="text.element">{{ text.element }}</option>
              <option v-for="item in text.elementList" :key="item" :value="item" v-text="item"></option>
            </select>
            <select class="form-select mt-2" v-model="classFilter" @click="clearSearch">
              <option selected :value="text.class">{{ text.class }}</option>
              <option v-for="item in text.classList" :key="item" :value="item" v-text="item"></option>
            </select>
          </div>
          <div id="nav-scroll">
            <router-link 
              v-for="item in unitsFilter" :key="item.name"
              class="btn btn-outline-secondary nav-link my-1 mx-3 me-md-4"
              @click.prevent="initialUnit(item)"
              @mouseover="showIcon(item)"
              @mouseleave="hideIcon()"
              :to="{ name: 'heroName', params: { heroName: item.displayName }}">
              {{ item.name }}
            </router-link>
          </div>
          <div style="position: absolute; top: 50%; left: 100%; width: 120%;">
            <img :src="selectIcon" alt="" style="width: 30%;" v-show="isSelectIcon">
          </div>
        </nav>
    </div>
</template>

<script>
/* eslint-disable */
import { ref } from 'vue'
import { computed } from '@vue/runtime-core'
import { storeToRefs } from 'pinia'
import { useMobile } from '@/store/Mobile'
import { useLanguage } from '@/store/Language'
import { useInitUnit } from '@/store/InitUnit'
import { useSkill } from "@/store/Skill"

export default {
    name: 'SideNav',
    setup() {
        const { isSideNav } = storeToRefs(useMobile())
        const { text } = storeToRefs(useLanguage())
        const classList = computed(() => {
          return text.value.classList
        })
        const elementList = ref(text.value.elementList)
        const toggleSideNav = () => {
            useMobile().toggleSideNav()
        }

        const {
          units,
          searchFilter,
          classFilter,
          elementFilter,
          clearSearch,
        } = storeToRefs(useInitUnit())

        classFilter.value = text.value.class
        elementFilter.value = text.value.element

        const selectIcon = ref('')
        const isSelectIcon = ref(false)
        const showIcon = (item) => {
          isSelectIcon.value = true
          selectIcon.value = `img/UI_Card_S/UI_Card_${item.imgName}_S.png`
        }

        const hideIcon = () => {
          isSelectIcon.value = false
          selectIcon.value = ''
        }
        const unitsFilter = computed(() => {
          console.log(units.value)
          return units.value.filter((item) => {
            if (searchFilter.value) {
              classFilter.value = text.value.class
              elementFilter.value = text.value.element
            }

            return ((classFilter.value === item.classType) || (classFilter.value === text.value.class))
            && ((elementFilter.value === item.elementType) || (elementFilter.value === text.value.element))
            && (item.displayName.toLowerCase().includes(searchFilter.value.toLowerCase()))
          })
        })
        // const unitsFilter = computed(() => {
        //   // console.log(Unit_language.mSource.mTerms
        //   //   .filter(item => item.Term.includes("UnitName/CHAR"))
        //   //   .sort(function(a, b){
        //   //     if (a.Languages[0] < b.Languages[0]) {return -1;}
        //   //     if (a.Languages[0] > b.Languages[0]) {return 1;}
        //   //     return 0;
        //   //   }))
        //   return Unit_language.mSource.mTerms
        //     .filter(item => item.Term.includes("UnitName/CHAR"))
        //     .sort(function(a, b){
        //       if (a.Languages[0] < b.Languages[0]) {return -1;}
        //       if (a.Languages[0] > b.Languages[0]) {return 1;}
        //       return 0;
        //     })
        //     // .filter((item) => {
        //     //   if (searchFilter.value) {
        //     //     classFilter.value = useLanguage().text.class
        //     //     elementFilter.value = useLanguage().text.element
        //     //   }

        //     //   return ((classFilter.value === item.classType) || (classFilter.value === 'Class'))
        //     //   && ((elementFilter.value === item.elementType) || (elementFilter.value === 'Element'))
        //     //   && (item.displayName.toLowerCase().includes(searchFilter.value.toLowerCase()))
        //     // })
        // })

        const initialUnit = (item) => {
          // 選擇角色並縮回側欄
            document.querySelectorAll(".carousel-item").forEach(obj => obj.classList.remove("active"))
            document.getElementById("ui-card-carousel-item-1").classList.add('active') // 重置carousel
            document.getElementById("spine-SD-carousel-item-1").classList.add('active') // 重置carousel
            useInitUnit().initialUnit(item)
            useSkill().setSkill()
            toggleSideNav()
            // useSpine().setSpine_SD()
        }
        return {
            isSideNav,
            classList,
            elementList,
            text,
            selectIcon,
            isSelectIcon,
            showIcon,
            hideIcon,
            searchFilter,
            elementFilter,
            classFilter,
            clearSearch,
            unitsFilter,
            initialUnit
        }
    }
}
</script>

<style scoped>
.nav-link {
    color: black;
}
#nav-scroll {
    overflow-y: scroll; 
    overflow-x: hidden; 
    height: 100vh;
}

@media screen and (max-width: 768px) {
    .side-nav{
        position:fixed;
        overflow:hidden;
        background-color: rgba(255, 255, 255, 0.9);
        z-index: 10;
        width:100%;
        left:-100%;
        transition:left 1.5s;
    }
    .side-nav.out{
        left:0;
    }
    .side-nav.in {
        left:-100%;
    }
    #nav-scroll {
        height: 55vh;
    }
}
</style>