<template>
    <div class="row justify-content-center">
        <div class="col-md-auto offset-md-auto col-11">
        <table class="table table-hover table-dark rounded">
            <thead>
                <tr>
                    <th scope="col" style="width: 30%;">
                        <h4 v-text="text.ability"></h4>
                    </th>
                    <th scope="col">
                        <div class="input-group d-flex justify-content-end" role="group" @click="renderSkill">
                            <span class="input-group-text">{{ text.level }}:</span>
                            <button type="button" class="btn btn-outline-light btn-sm" :class="{ active: skillLevel === 1 }" @click="skillLevel = 1">1</button>
                            <button type="button" class="btn btn-outline-light btn-sm" :class="{ active: skillLevel === 2 }" @click="skillLevel = 2">2</button>
                            <button type="button" class="btn btn-outline-light btn-sm" :class="{ active: skillLevel === 3 }" @click="skillLevel = 3">3</button>
                            <button type="button" class="btn btn-outline-light btn-sm" :class="{ active: skillLevel === 4 }" @click="skillLevel = 4">4</button>
                            <button type="button" class="btn btn-outline-light btn-sm" :class="{ active: skillLevel === 5 }" @click="skillLevel = 5">5</button>
                        </div>
                    </th>
                </tr>
            </thead>
            <tbody v-if="isMobile">
                <tr v-if="nameActive" class="table-group-divider">
                    <th scope="row" colspan="2">
                        <div class="d-flex">
                            <span v-text="nameActive"></span>
                            <span>&nbsp;/&nbsp;</span>
                            <span v-text="text.active"></span>
                            <span v-text="activeCooldown" class="text-info ms-auto"></span>
                            <span v-text="text.cd" class="text-info"></span>
                        </div>
                    </th>
                </tr>
                <tr v-if="nameActive">
                    <td scope="row" colspan="2" v-html="active"></td>
                </tr>
                <tr v-if="namePassive1" class="table-group-divider">
                    <th scope="row" colspan="2">
                        <span v-text="namePassive1"></span>
                        <span> / </span>
                        <span v-text="text.passive"></span>
                    </th>
                </tr>
                <tr v-if="namePassive1">
                    <td scope="row" colspan="2" v-html="passive"></td>
                </tr>
                <tr>
                    <th scope="col" colspan="2" style="width: 30%;">
                        <h4 v-text="text.trait"></h4>
                    </th>
                </tr>
                <tr v-if="nameTrait1" class="table-group-divider">
                    <th scope="row" colspan="2">{{nameTrait1}}</th>
                </tr>
                <tr v-if="nameTrait1">
                    <td scope="row" colspan="2" v-html="descriptionTrait1"></td>
                </tr>
                <tr v-if="nameTrait2" class="table-group-divider">
                    <th scope="row" colspan="2">{{nameTrait2}}</th>
                </tr>
                <tr v-if="nameTrait2">
                    <td scope="row" colspan="2" v-html="descriptionTrait2"></td>
                </tr>
                <tr v-if="nameTrait3" class="table-group-divider">
                    <th scope="row" colspan="2">{{nameTrait3}}</th>
                </tr>
                <tr v-if="nameTrait3">
                    <td scope="row" colspan="2" v-html="descriptionTrait3"></td>
                </tr>
                <tr v-if="nameTrait4" class="table-group-divider">
                    <th scope="row" colspan="2">{{nameTrait4}}</th>
                </tr>
                <tr v-if="nameTrait4">
                    <td scope="row" colspan="2" v-html="descriptionTrait4"></td>
                </tr>
            </tbody>
            <tbody v-else>
                <tr v-if="nameActive">
                    <th scope="row" class="table-group-divider">
                        <span v-text="nameActive"></span>
                        <span> / </span>
                        <span v-text="text.active"></span>
                        <br>
                        <span v-text="activeCooldown" class="text-info"></span>
                        <span v-text="text.cd" class="text-info"></span>
                    </th>
                    <td v-html="active"></td>
                </tr>
                <tr v-if="namePassive1">
                    <th scope="row">
                        <span v-text="namePassive1"></span>
                        <span> / </span>
                        <span v-text="text.passive"></span>
                    </th>
                    <td v-html="passive"></td>
                </tr>
                <tr>
                    <th scope="col" colspan="2" style="width: 30%;">
                    <h4 v-text="text.trait"></h4>
                    </th>
                </tr>
                <tr v-if="nameTrait1">
                    <th scope="row" class="table-group-divider">{{nameTrait1}}</th>
                    <td v-html="descriptionTrait1"></td>
                </tr>
                <tr v-if="nameTrait2">
                    <th scope="row">{{nameTrait2}}</th>
                    <td v-html="descriptionTrait2"></td>
                </tr>
                <tr v-if="nameTrait3">
                    <th scope="row">{{nameTrait3}}</th>
                    <td v-html="descriptionTrait3"></td>
                </tr>
                <tr v-if="nameTrait4">
                    <th scope="row">{{nameTrait4}}</th>
                    <td v-html="descriptionTrait4"></td>
                </tr>
            </tbody>
        </table>
        </div>
    </div>
</template>

<script>
/* eslint-disable */
// import { ref, onMounted } from 'vue'
import { useMobile } from "@/store/Mobile"
import { useLanguage } from '@/store/Language'
import { useSkill } from "@/store/Skill"
import { storeToRefs } from 'pinia'

export default {
    name: 'UnitSkill',
    components: {
    },
    setup() {
        const { isMobile } = storeToRefs(useMobile())
        const { text } = storeToRefs(useLanguage())
        const {
            unit,
            skillLevel,
            nameActive,
            namePassive1,
            nameTrait1,
            nameTrait2,
            nameTrait3,
            nameTrait4,
            nameTrait5,
            descriptionTrait1,
            descriptionTrait2,
            descriptionTrait3,
            descriptionTrait4,
            descriptionTrait5,
            active,
            passive,
            activeCooldown,
        } = storeToRefs(useSkill())

        const renderSkill = () => {
            useSkill().renderSkill()
        }
        return {
            isMobile,
            text,
            unit,
            skillLevel,
            nameActive,
            namePassive1,
            nameTrait1,
            nameTrait2,
            nameTrait3,
            nameTrait4,
            nameTrait5,
            descriptionTrait1,
            descriptionTrait2,
            descriptionTrait3,
            descriptionTrait4,
            descriptionTrait5,
            active,
            passive,
            activeCooldown,
            renderSkill
        }
    }
}
</script>

<style scoped>
table {
    opacity: 0.9;
    table-layout:fixed;
}
</style>
