<template>
    <div class="container">
      <div class="row">
        <SideNav/>
        <MainUnit/>
      </div>
    </div>
</template>

<script>
/* eslint-disable */
import { useRoute, useRouter } from 'vue-router'
import SideNav from '@/components/SideNav.vue'
import MainUnit from '@/components/MainUnit.vue'
import { useLanguage } from '@/store/Language'
import { useInitUnit } from "@/store/InitUnit"
import { useSkill } from "@/store/Skill"
import { storeToRefs } from 'pinia'

export default {
  name: 'HomeView',
  components: {
      SideNav,
      MainUnit,
  },
  setup() {
    const { language } = storeToRefs(useLanguage())
    const { units, unit } = storeToRefs(useInitUnit())
    if (useRoute().query.lang != undefined) {
      language.value = Math.max(0, ['en', 'ko', 'ja'].indexOf(useRoute().query.lang))
    }
    unit.value = useRoute().params.heroName === undefined
      ? units.value[0]
      : units.value[units.value.findIndex(obj => 
          (obj.displayName.toLowerCase() === useRoute().params.heroName.toString().toLowerCase()) || 
          (obj.srcName.toLowerCase() === useRoute().params.heroName.toString().toLowerCase()) || 
          (obj.imgName.toLowerCase() === useRoute().params.heroName.toString().toLowerCase())
        )]
    if (unit.value === undefined) {
      unit.value = units.value[0]
      useLanguage().changeLanguage(language.value)
      useInitUnit().setLanguage()
      useInitUnit().initialUnit()
      useSkill().setSkill()
      useRouter().push({ path:'/' })
    } else {
      useLanguage().changeLanguage(language.value)
      useInitUnit().setLanguage()
      useInitUnit().initialUnit()
      useSkill().setSkill()
    }
  }
}
</script>
